import React, { useState, useEffect } from "react";

// components

import firebaseConfig from "firebaseConfig";
const storage = firebaseConfig.storage;
const database = firebaseConfig.database;
export default function CardNews() {
  const [progress, setProgress] = useState(0);
  const [image, setImage] = useState(null);
  const [picture, setPicture] = useState("");
  const [id, setId] = useState("");
  const [happy, setHappy] = useState("");
  const [link, setlink] = useState("");
  const [time, setTime] = useState([]);
  const [title, setTitle] = useState("");
  const handleChange = (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0]);
    }
  };

  var today = new Date();
  var date =
    today.getDate() + "-" + (today.getMonth() + 1) + "-" + today.getFullYear();

  const handleUpload = () => {
    if (image != null && title != "") {
      alert("thêm tin tức thành công");
      const uploadTask = storage.ref(`images/${image.name}`).put(image);
      uploadTask.on(
        "state_changed",
        (snapshot) => {
          const progress = Math.round(
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100
          );
          setProgress(progress);
        },
        (error) => {
          console.log(error);
        },
        () => {
          storage
            .ref("images")
            .child(image.name)
            .getDownloadURL()
            .then((value) => {
              setPicture(value);

              database
                .ref("News")
                .push()
                .set({
                  picture: value,
                  id: database.ref("News").push().key,
                  happy: happy,
                  time: date,
                  title: title,
                  link: link,
                });
            });
        }
      );
    } else {
      alert("Lỗi chưa có dữ liệu");
    }
  };

  return (
    <>
      <div className="relative flex flex-col min-w-0 break-words bg-white mb-6 shadow-lg rounded max-w-580-px">
        <div className="rounded-t mb-0 px-4 py-3 border-0">
          <div className="flex flex-wrap items-center">
            <div className="flex-auto px-4 lg:px-10 py-10 pt-0">
              <form>
                <h6 className="text-gray-500 text-sm mt-3 mb-6 font-bold uppercase">
                  Announcements and News
                </h6>
                <div className="flex flex-wrap">
                  <div className="w-full lg:w-12/12 px-4">
                    <div className="relative w-full mb-3">
                      <label
                        className="block uppercase text-gray-700 text-xs font-bold mb-2"
                        htmlFor="grid-password"
                      >
                        title
                      </label>
                      <input
                        type="text"
                        onChange={(event) => setTitle(event.target.value)}
                        className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                        placeholder="title"
                      />
                    </div>
                  </div>
                </div>

                <hr className="mt-6 border-b-1 border-gray-400" />

                <h6 className="text-gray-500 text-sm mt-3 mb-6 font-bold uppercase">
                  news content
                </h6>
                <div className="flex flex-wrap">
                  <div className="w-full lg:w-12/12 px-4">
                    <div className="relative w-full mb-3">
                      <input
                        type="text"
                        onChange={(event) => setHappy(event.target.value)}
                        className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                        placeholder="Nội dung"
                      />
                    </div>
                  </div>
                </div>

                <hr className="mt-6 border-b-1 border-gray-400" />
                <h6 className="text-gray-500 text-sm mt-3 mb-6 font-bold uppercase">
                  link attached
                </h6>
                <div className="flex flex-wrap">
                  <div className="w-full lg:w-12/12 px-4">
                    <div className="relative w-full mb-3">
                      <input
                        type="text"
                        onChange={(event) => setlink(event.target.value)}
                        className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                        placeholder="https/"
                      />
                    </div>
                  </div>
                </div>
                <hr className="mt-6 border-b-1 border-gray-400" />
                <progress
                  style={{ marginTop: 20, marginBottom: 5 }}
                  className="w-full lg:w-12/12 px-4"
                  value={progress}
                  max="100"
                />
                <input type="file" onChange={handleChange}></input>
                <hr className="mt-6 border-b-1 border-gray-400" />
                <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
                  <button
                    onClick={handleUpload}
                    style={{ marginTop: 5 }}
                    className="bg-gray-900 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                  >
                    save
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
