import React, { useState, useEffect } from "react";
import _ from "lodash";

import firebaseConfig from "firebaseConfig";
const storage = firebaseConfig.storage;
const database = firebaseConfig.database;
// components

export default function CardPannerApp() {
  const [image, setImage] = useState(null);
  const [progress, setProgress] = useState(0);
  const [listapp, setlistapp] = useState([]);
  const [url, setUrl] = useState("");
  const [id, setid] = useState("");
  const handleChange = (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0]);
    }
  };

  const chekdata = useEffect(() => {
    const starCountRef = database.ref("CuaHang/");
    starCountRef.on("value", function (snapshot) {
      const value = snapshot.val();
      const array = _.map(value, (val) => {
        return { ...val };
      });
      setlistapp(array);
    });
  }, []);

  const handleUpload = () => {
    if (image != null) {
      const uploadTask = storage.ref(`images/${image.name}`).put(image);
      uploadTask.on(
        "state_changed",
        (snapshot) => {
          const progress = Math.round(
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100
          );
          setProgress(progress);
        },
        (error) => {
          console.log(error);
        },
        () => {
          storage
            .ref("images")
            .child(image.name)
            .getDownloadURL()
            .then((value) => {
              setUrl(value);
              database.ref("Banner").child("BannerApp").push().set({
                url: value,
                id: id,
              });
            });
        }
      );
      alert("Thêm Panner App thành công ");
    } else {
      alert("Lỗi chưa chọn hình ảnh hoặc mã của hàng");
    }
  };

  return (
    <>
      <div className="relative flex flex-col min-w-0 break-words bg-white mb-6 shadow-lg rounded max-w-580-px">
        <div className="rounded-t mb-0 px-4 py-3 border-0">
          <div className="flex flex-wrap items-center">
            <div className="relative w-full px-4 max-w-full flex-grow flex-1">
              <progress
                className="w-full lg:w-12/12 px-4"
                value={progress}
                max="100"
              />
              <input type="file" onChange={handleChange}></input>
              <img
                src={url || "http://via.placeholder.com/300"}
                alt="firebase-image"
              />
              <select value={id} onChange={(e) => setid(e.target.value)}>
                {listapp.map((value, index) => {
                  return (
                    <option key={index} value={value.macuahang}>
                      {value.tencuahang}
                    </option>
                  );
                })}
              </select>
            </div>
            <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
              <button
                onClick={handleUpload}
                className="bg-indigo-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                type="button"
              >
                save
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
