import axios from "axios";

export const getpush = async (body, title, tokens) => {
  try {
    let postpush = await axios.post(
      "http://localhost:5000/tvs/notification",
      {
        body,
        title,
        tokens,
      }
    );
    console.log("====================================");
    console.log(postpush);
    console.log("====================================");
    return postpush;
  } catch (error) {
    console.log("====================================");
    console.log(error);
    console.log("====================================");
    return null;
  }
};
