import React, { useState, useEffect } from "react";
import _ from "lodash";
import { GoogleMap, LoadScript } from "@react-google-maps/api";
// components
import firebaseConfig from "firebaseConfig";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import CardSocialTraffic from "components/Cards/CardSocialTraffic.js";

import MapExample from "components/Maps/MapExample.js";

const database = firebaseConfig.database;

export default function Cardnewsinformation(props) {
  const addItemcode = useSelector((state) => state.GetNews.listnews);

  const [listMenu, setlistMenu] = useState([]);

  useEffect(() => {
    setlistMenu([addItemcode]);
  }, []);
  console.log("====================================", listMenu);

  return (
    <>
      <div className="flex flex-wrap ">
        <div className=" flex flex-wrap relative xl:w-12/12 w-full mb-12 px-4 ">
          <div className="bg-white min-w-0 break-words w-full mb-2 shadow-lg rounded xl:w-12/12  ">
            {listMenu.map((index) => {
              return (
                <div key={index}>
                  <div className=" xl:w-12/12 justify-center">
                    <img
                      style={{ width: "500px" }}
                      src={index.picture}
                      alt="..."
                    />
                  </div>
                  <div className=" px-2 py-2">
                    <div className="text-gray-600 text-sm font-bold">
                      Title: {index.title}
                    </div>
                    <div> {index.time}</div>

                    <a
                      href={index.link}
                      className="text-gray-700 hover:text-gray-900 text-sm font-semibold block "
                    >
                      link đính kèm: {index.link}
                    </a>
                    <div style={{ margin: "30px" }}> Content {index.happy}</div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </>
  );
}
