import React, { useState, useEffect } from "react";
import _ from "lodash";
import axios from "axios";
import MapExample from "components/Maps/MapExample.js";
import FreeScrollBar from "react-free-scrollbar";
import { Link } from "react-router-dom";
import { getpush } from "api/push";
import firebaseConfig from "firebaseConfig";
const storage = firebaseConfig.storage;
const database = firebaseConfig.database;

export default function Notification() {
  var newArray;
  const [body, setbody] = useState("");
  const [dataS, setDataS] = useState([]);
  const [title, settitle] = useState([]);
  const [tokens, settokens] = useState([]);
  const [Arraydata, setArraydata] = useState([]);
  const [tokens1, settokens1] = useState([]);
  const [dataList, setDataList] = useState([]);
  const chekdata = useEffect(() => {
    const starCountRef = database.ref("User/");
    starCountRef.on("value", function (snapshot) {
      const value = snapshot.val();
      const array = _.map(value, (val) => {
        return { ...val };
      });

      settokens(array);
      setDataList(array);
    });
  }, []);

  const addElement = async (item) => {
    newArray = [...dataS, { token: item.token, name: item.name }];

    await setDataS(newArray);
    const results = dataList.filter(
      ({ token: token1 }) =>
        !newArray.some(({ token: token2 }) => token2 === token1)
    );
    await setDataList(results);

    settokens1(newArray);
  };

  const pushnoti = async () => {
    if (title != "") {
      if (body != "") {
        if (tokens1 != "") {
          var data = [];
          for (var i = 0; i < tokens1.length; i++) {
            data[i] = tokens1[i].token;
          }

          var data1 = data.join();
          getpush(body, title, data1);
          alert("Gữi thông báo thành công");
        } else {
          alert("tokens chưa có dữ liệu");
        }
      } else {
        alert("body chưa có dữ liệu");
      }
    } else {
      alert("title chưa có dữ liệu");
    }
  };

  const Allpushnoti = async () => {
    if (title != "" && body != "") {
      var data = [];
      for (var i = 0; i < tokens.length; i++) {
        data[i] = tokens[i].token;
      }

      var data1 = data.join();

      getpush(body, title, data1);
      alert("Gữi thông báo thành công");
    } else {
      alert("title chưa có dữ liệu");
    }
  };

  const deleteCheck = async (item) => {
    const filteredMovies = dataS.filter((ss) => {
      return ss.token !== item.token;
    });
    await setDataS(filteredMovies);
    let newArray = [...dataList, { token: item.token, name: item.name }];
    await setDataList(newArray);
  };
  return (
    <>
      <div className="flex flex-wrap h-600-px">
        <div className="relative flex flex-col  w-full mb-6 px-4 py-2">
          <div className="flex flex-wrap ">
            <div className="w-full flex flex-wrap ">
              <div
                className=" lg:w-5/12  px-4 shadow-lg rounded"
                style={{ backgroundColor: "#FFFFFF" }}
              >
                <div className="justify-center">
                  <h2
                    style={{
                      marginTop: 5,
                      marginBottom: 5,
                    }}
                  >
                    NOTIFICATION MANAGER{" "}
                  </h2>
                </div>

                <div className="relative w-full mb-3">
                  <form className="relative w-full mb-3">
                    <h4 className=" font-bold ">Title</h4>

                    <input
                      multiple={true}
                      type="text"
                      style={{
                        backgroundColor: "#F2F2F2",
                        height: 50,
                        width: 400,
                      }}
                      placeholder="Tiêu đề"
                      onChange={(event) => settitle(event.target.value)}
                      className="px-4 py-4   placeholder-gray-400 text-gray-700 bg-white rounded text-sm  focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                    />
                  </form>
                  <h4 className=" font-bold ">Content</h4>
                  <textarea
                    textAlignVertical="top"
                    type="text"
                    style={{
                      backgroundColor: "#F2F2F2",
                      height: 200,
                    }}
                    placeholder="Nội dung"
                    value={body}
                    onChange={(event) => setbody(event.target.value)}
                    className="px-4 py-4   placeholder-gray-400 text-gray-700 bg-white rounded text-sm  focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150"
                  />
                </div>

                <div className=" flex flex-wrap  " style={{ marginBottom: 10 }}>
                  <div
                    className=" shadow-lg rounded "
                    style={{ overflow: "auto", height: 100, width: 150 }}
                  >
                    {dataList.map((index, value) => {
                      return (
                        <div
                          className="flex py-2"
                          style={{
                            backgroundColor: "#F2F2F2",
                          }}
                        >
                          <Link
                            type="button"
                            className="btn btn-default flex "
                            onClick={(e) => {
                              addElement(index);
                            }}
                          >
                            <div>
                              <div
                                style={{
                                  width: 150,
                                  height: 20,
                                  color: "black",
                                  paddingLeft: 8,
                                }}
                              >
                                {index.name}
                              </div>
                            </div>
                          </Link>
                        </div>
                      );
                    })}
                  </div>
                  <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-left p-10">
                    <button
                      onClick={(e) => {
                        pushnoti();
                      }}
                      className="bg-gray-900  text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                      type="button"
                    >
                      push
                    </button>
                    <button
                      onClick={(e) => {
                        Allpushnoti();
                      }}
                      className="bg-gray-900 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                      type="button"
                    >
                      push all
                    </button>
                  </div>
                </div>
              </div>
              <div
                style={{ backgroundColor: "#FFFFFF", marginLeft: 30 }}
                className=" w-full  lg:w-8/12 shadow-lg rounded  "
              >
                <div className=" lg:w-12/12  flex flex-wrap  ">
                  {dataS.map((item) => (
                    <Link
                      className="flex flex-wrap m-5px "
                      key={item.token}
                      onClick={() => deleteCheck(item)}
                      style={{
                        backgroundColor: "#F2F2F2",
                        // width: 100,
                        borderRadius: 10,
                      }}
                    >
                      <div style={{ padding: 20, color: "#111111" }}>
                        {item.name}
                      </div>
                    </Link>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* </div> */}
    </>
  );
}
