import React, { useState, useEffect } from "react";
import _ from "lodash";
import firebaseConfig from "firebaseConfig";

import CardPannerWellcom from "components/Cards/CardPannerWellcom";
import CardPannerApp from "components/Cards/CardPannerApp";
import CardPannerPopup from "components/Cards/CardPannerPopup";
import { v4 as uuid } from "uuid";
import { Link } from "react-router-dom";
const database = firebaseConfig.database;
export default function Tables() {
  const storage = firebaseConfig.storage;
  const [listBannerApp, setListBannerApp] = useState([]);
  const [listBannerPopup, setlistBannerPopup] = useState([]);
  const [listWelloCome, setlistWelloCome] = useState([]);
  const [fileUrl, setFileUrl] = React.useState(null);
  const listRef = storage.ref("images/");
  const chekdata = useEffect(() => {
    const getBannerApp = database.ref("Banner/BannerApp");
    getBannerApp.on("value", function (snapshot) {
      const value = snapshot.val();
      const array = _.map(value, (val) => {
        return { ...val };
      });
      setListBannerApp(array);
    });
    const getpopup = database.ref("Banner/Popup");
    getpopup.on("value", function (snapshot) {
      const value = snapshot.val();
      const array = _.map(value, (val) => {
        return { val };
      });
      setlistBannerPopup(value);
      console.log("====================================");
      console.log("value", value);
      console.log("====================================");
    });
    const getWellCome = database.ref("Banner/Wellcome");
    getWellCome.on("value", function (snapshot) {
      const value = snapshot.val();
      const array = _.map(value, (val) => {
        return { val };
      });
      setlistWelloCome(array);
    });
  }, []);
  console.log("====================================");
  console.log("listWelloCome", listWelloCome);
  console.log("listBannerApp", listBannerApp);
  console.log("listBannerPopup", listBannerPopup);
  console.log("====================================");
  return (
    <>
      <div className=" flex flex-wrap relative xl:w-12/12 w-full  mb-12 px-4 ">
        <div className="bg-white min-w-0 break-words w-full mb-6 shadow-lg rounded xl:w-8/12">
          <div className="flex flex-wrap py-2">
            <div className=" px-2 py-2">
              <div className="w-full lg:w-12/12 px-4">
                <div className="relative w-full mb-3">
                  <label
                    className="block uppercase text-gray-700 text-xs font-bold mb-2"
                    htmlFor="grid-password"
                  >
                    Banner WelloCome
                  </label>
                </div>
              </div>
              {listWelloCome.map((index, value) => {
                return (
                  <div key={index} className="flex flex-wrap py-2">
                    <div>
                      <img
                        src={index.val}
                        alt="..."
                        style={{ width: "200px" }}
                        // className="shadow-lg rounded .max-w-100-px h-20 align-middle border-none"
                      />
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        </div>
        <div className="w-full xl:w-4/12 px-4">
          <CardPannerWellcom />
        </div>
        <div className="bg-white min-w-0 break-words w-full mb-6 shadow-lg rounded xl:w-8/12">
          <div className="flex flex-wrap py-2">
            <div className=" px-2 py-2">
              <div className="flex flex-wrap py-2">
                <div className="w-full lg:w-12/12 px-4">
                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-gray-700 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      Banner Popup
                    </label>
                  </div>
                </div>
                <div>
                  <img
                    src={listBannerPopup.url}
                    alt="..."
                    style={{ width: "200px" }}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="w-full xl:w-4/12 px-4">
          <CardPannerPopup />
        </div>
        <div className="min-w-0 break-words w-full mb-6 shadow-lg rounded xl:w-8/12 bg-white">
          <div className="w-full lg:w-12/12 px-4 ">
            <div className="relative w-full mb-3 ">
              <label
                className="block uppercase text-gray-700 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                Banner App
              </label>
            </div>
          </div>

          <div className="flex flex-wrap xl:w-12/12">
            {listBannerApp.map((index, value) => {
              return (
                <div className="flex mx-auto p-2">
                  <img src={index.url} style={{ width: "200px" }} />
                </div>
              );
            })}
          </div>
        </div>
        <div className="w-full xl:w-4/12 px-4">
          <CardPannerApp />
        </div>
      </div>
    </>
  );
}
