import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";

// components

import AdminNavbar from "components/Navbars/AdminNavbar.js";
import Sidebar from "components/Sidebar/Sidebar.js";
import HeaderStats from "components/Headers/HeaderStats.js";
import FooterAdmin from "components/Footers/FooterAdmin.js";
import Cardnewsinformation from "components/Cards/Cardnewsinformation";
// views

import Dashboard from "views/admin/Dashboard.js";
import Notification from "views/admin/Notification";
import Customer from "views/admin/Customer.js";
import Tables from "views/admin/Tables.js";
import Storedetails from "views/admin/Storedetails";
import Color from "views/admin/Color";
import New from "views/admin/News";
import Chart from "views/admin/ChartPage";
// import Demo from "views/admin/demo.js";
export default function Admin() {
  return (
    <>
      <Sidebar />
      <div className="relative md:ml-64 bg-gray-200">
        <AdminNavbar />
        {/* Header */}
        <HeaderStats />

        <div className="px-4 md:px-10 mx-auto w-full -m-24">
          <Switch style={{ position: "raletive" }}>
            <Route path="/admin/dashboard" exact component={Dashboard} />
            <Route path="/admin/Notification" exact component={Notification} />
            <Route path="/admin/CustomerManager" exact component={Customer} />
            <Route path="/admin/Banner" exact component={Tables} />
            <Route path="/admin/color" exact component={Color} />
            <Route path="/admin/new" exact component={New} />
            <Route path="/admin/Chart" exact component={Chart} />
            <Route
              path="/admin/dashboard/Storedetails"
              exact
              component={Storedetails}
            />
            <Route
              path="/admin/new/newsinformation"
              exact
              component={Cardnewsinformation}
            />
            <Redirect from="/admin" to="/admin/dashboard" />
          </Switch>
          <FooterAdmin
            style={{ position: "absolute", bottom: "20px", right: "30px" }}
          />
        </div>
      </div>
    </>
  );
}
