export const addItemcode = (Itemcode) => {
  return {
    type: "ADD_Itemcode",
    payload: Itemcode,
  };
};
export const getallItemcode = (AllItemcode) => {
  return {
    type: "GET_ALLItemcode",
    payload: AllItemcode,
  };
};
export const getnews = (news) => {
  return {
    type: "GET_ALLNew",
    payload: news,
  };
};
