const initialHeroState = {
  list: "",
  listnews: "",
  listAll: "",
  params: { _limit: 10, _page: 1 },
  loading: false,
  error: "",
  activeHero: {},
};
const ItemcodeReducer = (state = initialHeroState, action) => {
  switch (action.type) {
    case "ADD_Itemcode": {
      return {
        ...state,
        list: action.payload,
      };
    }
    case "GET_ALLItemcode": {
      return {
        ...state,
        listAll: action.payload,
      };
    }

    case "GET_ALLNew": {
      return {
        ...state,
        listnews: action.payload,
      };
    }
    default:
      return state;
  }
};

export default ItemcodeReducer;
