import React, { useState, useEffect } from "react";
import CardNews from "components/Cards/CardNews.js";
import { SketchPicker } from "react-color";
import firebaseConfig from "firebaseConfig";
import _ from "lodash";
import { Link } from "react-router-dom";
import { Alert } from "antd";
import { addItemcode, getallItemcode, getnews } from "actions/ItemcodeActions";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
const database = firebaseConfig.database;
export default function News() {
  let history = useHistory();
  const [listNews, setlistNews] = useState([]);
  useEffect(() => {
    const starCountRef = database.ref("News/");
    starCountRef.on("value", function (snapshot) {
      const value = snapshot.val();
      const array = _.map(value, (val) => {
        return { ...val };
      });
      setlistNews(array);
    });
  }, []);
  const dispatch = useDispatch();

  const onClickMe = (index) => {
    const action = getnews(index);

    history.push("/admin/new/newsinformation");
    dispatch(action);
  };
  // const onClickdelete = (index) => {

  //   const starCountRef = database.ref("News/");

  //   starCountRef.child(index.id).remove();

  // };
  return (
    <>
      <div className="flex flex-wrap ">
        <div className=" flex flex-wrap relative xl:w-12/12 w-full mb-12 px-4 ">
          <div className="bg-white break-words w-full mb-6 shadow-lg rounded xl:w-8/12">
            {listNews.map((index) => {
              return (
                <div key={index} className="flex flex-wrap py-2">
                  <Link
                    type="button"
                    className="btn btn-default flex px-4 bottom-10"
                    label={index.macuahang}
                    onClick={(e) => {
                      onClickMe(index);
                    }}
                  >
                    <div>
                      <img
                        src={index.picture}
                        alt="..."
                        style={{ width: "200px" }}
                      />
                    </div>
                    <div className=" px-2 py-2">
                      <div
                        style={{ fontSize: 20, marginTop: 10, color: "#000" }}
                      >
                        {" "}
                        Tile: {index.title}
                      </div>
                      <div
                        style={{ fontSize: 18, marginTop: 10, color: "#000" }}
                      >
                        {" "}
                        Thời gian: {index.time}
                      </div>
                    </div>
                  </Link>
                </div>
              );
            })}
          </div>
          <div className="w-full xl:w-4/12 px-4">
            <CardNews />
          </div>
        </div>
      </div>
    </>
  );
}
