import React, { useState, useEffect } from "react";

import { SketchPicker } from "react-color";
import firebaseConfig from "firebaseConfig";
import { Alert } from "antd";
const database = firebaseConfig.database;
export default function Color() {
  const [color, setcolor] = useState("#ff0000");
  const [backgroundColor, setbackgroundColor] = useState("#ff0000");
  const [listRestaurant, setListRestaurant] = useState([]);

  const writeUserData = (backgroundColor, color) => {
    try {
      database.ref("Mamau/").set({
        backgroundColor: backgroundColor,
        colorText: color,
      });
      alert("Thanh công");
    } catch (error) {
      alert("Lỗi" + error);
    }
  };

  return (
    <div className="flex flex-wrap ">
      <div className=" flex flex-wrap relative xl:w-12/12 w-full mb-12 px-4 ">
        <div className="bg-white min-w-0 break-words w-full mb-6 shadow-lg rounded xl:w-12/12 justify-center">
          <div className="flex items-center justify-center">
            <div>
              <label
                className="block uppercase text-gray-700 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                COLOR
              </label>

              <SketchPicker
                color={color}
                onChangeComplete={(color) => {
                  setcolor(color.hex);
                }}
              />
              <div className="flex flex-wrap">
                <div className="w-full lg:w-12/12 px-4">
                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-gray-700 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      color
                    </label>
                    <label className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150">
                      {color}
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div
              style={{
                backgroundColor: backgroundColor,
                height: "300px",
                width: "300px",
                paddingTop: 30,
                fontSize: 50,
                color: color,
              }}
            >
              TEXT
            </div>
            <div>
              <label
                className="block uppercase text-gray-700 text-xs font-bold mb-2"
                htmlFor="grid-password"
              >
                BACKGROUNDCOLOR
              </label>
              <SketchPicker
                color={backgroundColor}
                onChangeComplete={(backgroundColor) => {
                  setbackgroundColor(backgroundColor.hex);
                }}
              />
              <div className="flex flex-wrap">
                <div className="w-full lg:w-12/12 px-4">
                  <div className="relative w-full mb-3">
                    <label
                      className="block uppercase text-gray-700 text-xs font-bold mb-2"
                      htmlFor="grid-password"
                    >
                      backgroundColor
                    </label>
                    <label className="px-3 py-3 placeholder-gray-400 text-gray-700 bg-white rounded text-sm shadow focus:outline-none focus:shadow-outline w-full ease-linear transition-all duration-150">
                      {" "}
                      {backgroundColor}
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="relative w-full px-4 max-w-full flex-grow flex-1 text-right">
            <button
              onClick={(e) => {
                writeUserData(backgroundColor, color);
              }}
              className="bg-indigo-500 text-white active:bg-indigo-600 text-xs font-bold uppercase px-3 py-1 rounded outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
              type="button"
            >
              save
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
