import React, { useState, useEffect, useRef } from "react";
import Chart from "chart.js";
import _ from "lodash";

import firebaseConfig from "firebaseConfig";

const database = firebaseConfig.database;
export default function ChartPage() {
  const [id, setid] = useState("");
  const [listchar, setlistchar] = useState([]);
  const [title, settitle] = useState([]);
  useEffect(() => {
    handleChart();
  }, []);

  const handleChart = () => {
    try {
      const starCountRef = database.ref("ViewedCount/");
      starCountRef.on("value", function (snapshot) {
        const value = snapshot.val();
        const result = Object.keys(value).map((key) => [
          Number(key),
          value[key],
        ]);
        const arr = result.map((item) => {
          return Object.keys(item[1]).map((key) => item[1][key]);
        });

        const arrName = result.map((value) => {
          return value[0];
        });
        // const datatesst = "2020";

        var today = new Date();
        var date = today.getFullYear();
        var datasets;
        const randomColor = Math.floor(Math.random() * 16777215).toString(16);
        for (var i = 0; i < arrName.length; i++) {
          if (date == arrName[i]) {
            datasets = [
              {
                data: arr[i],
                label: arrName[i],
                // borderColor: `#${randomColor}`,
                borderColor: `#EE0000`,
                fill: false,
              },
            ];
            console.log("====================================");
            console.log(datasets);
            // console.log(arr);
            console.log("====================================");
          }
        }

        setlistchar(result);

        let config = {
          type: "line",
          data: {
            labels: [
              "T1",
              "T2",
              "T3",
              "T4",
              "T5",
              "T6",
              "T7",
              "T8",
              "T9",
              "T10",
              "T11",
              "T12",
            ],
            datasets,
          },
          options: {
            title: {
              display: true,
              text: "Number of people logged in by month (number)",
            },
            legend: {
              display: true,
              width: 200,

              position: "bottom",
            },
          },
        };
        let ctx = document.getElementById("bar-chart").getContext("2d");
        window.myBar = new Chart(ctx, config);
      });
    } catch (error) {}
  };

  const datasets = listchar.map((value) => {
    const randomColor = Math.floor(Math.random() * 16777215).toString(16);
    const arrValue = Object.keys(value[1]).map((key) => value[1][key]);

    return {
      value,
    };
  });

  const onChange = (value) => {
    const datasets1 = listchar.map((value) => {
      const randomColor = Math.floor(Math.random() * 16777215).toString(16);
      const arrValue = Object.keys(value[1]).map((key) => value[1][key]);

      return {
        data: arrValue,
        label: value[0],
        // borderColor: `#${randomColor}`,
        borderColor: `#EE0000`,
        fill: false,
      };
    });
    const datasets = datasets1.filter((datasets1) => datasets1.label == value);

    let config = {
      type: "line",
      data: {
        labels: [
          "T1",
          "T2",
          "T3",
          "T4",
          "T5",
          "T6",
          "T7",
          "T8",
          "T9",
          "T10",
          "T11",
          "T12",
        ],
        datasets,
      },
      options: {
        title: {
          display: true,
          text: "Number of people logged in by month (number)",
        },
        legend: {
          display: true,
          position: "bottom",
        },
      },
    };
    let ctx = document.getElementById("bar-chart").getContext("2d");
    window.myBar = new Chart(ctx, config);
  };
  return (
    <>
      <div className="flex flex-wrap ">
        <div className=" flex flex-wrap relative xl:w-12/12 w-full   mb-12 px-4 ">
          <div
            className="bg-white break-words mb-6 shadow-lg rounded "
            style={{ width: "100%", height: 500 }}
          >
            <div>
              {/* Chart */}
              <div>
                <canvas
                  style={{ width: "100%", height: 500 }}
                  id="bar-chart"
                ></canvas>
              </div>
              <div style={{ margin: 10 }}>
                <h1>
                  Năm:{" "}
                  <select
                    value={id}
                    onChange={(e) => {
                      onChange(e.target.value);
                      setid(e.target.value);
                    }}
                  >
                    {datasets.map((value, index) => {
                      return (
                        <option key={index} value={value.value[0]}>
                          {value.value[0]}
                        </option>
                      );
                    })}
                  </select>
                </h1>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
