import React, { useState, useEffect } from "react";
import _ from "lodash";
import { GoogleMap, LoadScript } from "@react-google-maps/api";
// components
import firebaseConfig from "firebaseConfig";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import CardSocialTraffic from "components/Cards/CardSocialTraffic.js";

import MapExample from "components/Maps/MapExample.js";

const database = firebaseConfig.database;

export default function Storedetails(props) {
  const style = (
    <link
      rel="stylesheet"
      href="https://cdn.jsdelivr.net/npm/semantic-ui@2.4.1/dist/semantic.min.css"
    />
  );
  const addItemcode = useSelector((state) => state.Itemcode.list);
  const allItemcode = useSelector((state) => state.AllItemcode.listAll);
  const [listMenu, setlistMenu] = useState([]);
  const [listMenu1, setlistMenu1] = useState([]);

  useEffect(() => {
    const starCountRef = database.ref("Menu/");
    starCountRef.on("value", function (snapshot) {
      const value = snapshot.val();

      setlistMenu(value[addItemcode]);
    });
  }, []);

  console.log("====================================");
  const array = _.map(listMenu, (index) => {
    return (
      <div key={index} className="flex flex-wrap py-2">
        <div className="btn btn-default flex px-4 bottom-10">
          <div>
            <img
              src={index.hinhanh}
              alt="..."
              className="shadow-lg rounded max-w-10 h-20 align-middle border-none"
            />
          </div>
          <div className=" px-2 py-2">
            <div> Tên cửa hàng:{index.tenmonan}</div>
            <div> Mã món ăn: {index.mamonan}</div>
            <div> Địa chỉ: {index.gia}</div>
          </div>
        </div>
      </div>
    );
  });

  return (
    <>
      <div className="flex flex-wrap ">
        <div className=" flex flex-wrap relative xl:w-12/12 w-full ">
          <div className="bg-white break-words w-full mb-6 shadow-lg rounded xl:w-12/12">
            {array}
          </div>
          <div className="bg-white break-words w-full mb-6 shadow-lg rounded xl:w-12/12">
            <MapExample />
          </div>
          <div className="bg-white break-words w-full mb-6 shadow-lg rounded xl:w-12/12">
            <img
              alt="..."
              className="w-5 mr-1"
              src={require("assets/img/google.svg")}
            />
          </div>
        </div>
      </div>
    </>
  );
}
